<div class="wrapper">
    <h2>Login</h2>
    <form>
      <div class="input-box">
        <input type="text" placeholder="Enter your phone" required>
      </div>
      <div class="input-box">
        <input type="password" placeholder="Password" required>
      </div>
      <div class="input-box button">
        <input type="Submit"  @click="LoginUser()" value="Login">
      </div>
      <div class="text">
        <h3>If you don't have an account? <a @click="redirectRegisration()">Register now</a></h3>
      </div>
    </form>
  </div>
</template>
<script>
  // import axios from 'axios'
export default {
  name: 'loginScreen',
  data () {
    return {
      login:{}
    }
  },
  methods: {
    LoginUser () {
    // axios.post('/login',this.login)
    //     .then(response => {
    //       if(response.status == 200 && response.data.text == true){
    //         sessionStorage.setItem('authorization',response.headers.authorization);
