<template>
    <div>
        <v-container fluid class="px-3">
            <v-layout row wrap>
                <v-flex xs12>
                    <v-text-field v-model="customer.customerNameEn" label="Enter full Name" :error-messages="errors.collect('name')" v-validate="'required'" data-vv-name="name" required></v-text-field>
                    <v-text-field v-model="customer.mobileNumber" label="Enter Mobile Number" :error-messages="errors.collect('mobile')" v-validate="'required|max:10|numeric'" data-vv-name="mobile" :counter="10" required></v-text-field>
                     
                     <v-text-field v-model="customer.coustomerAddress" label="Enter Address" :error-messages="errors.collect('coustomerAddress')" v-validate="'required'" data-vv-name="coustomerAddress" required></v-text-field>
                     <v-text-field v-model="customer.coustomerQuery" label="Enter Query" :error-messages="errors.collect('coustomerQuery')" v-validate="'required'" data-vv-name="coustomerQuery" required></v-text-field>
                    <!-- <v-text-field v-model="customer.acountNo" label="Enter Account Number" :error-messages="errors.collect('accountno')" v-validate="'required|max:16|min:11|numeric'" data-vv-name="accountno" :counter="16" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.ifc" label="Enter IFSC Code" :error-messages="errors.collect('ifsc')" v-validate="{required:true,regex: '^[A-Za-z]{4}0[A-Z0-9a-z]{6}$'}" data-vv-name="ifsc" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.branch" label="Enter branch Name" :error-messages="errors.collect('branch')" v-validate="'required'" data-vv-name="branch" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.aadhaarNO" label="Enter aadhar Number " :error-messages="errors.collect('aadhar')" v-validate="'numeric'" data-vv-name="aadhar" :counter="12" v-on:blur="validateAdhar()" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.email" label="E-mail" :error-messages="errors.collect('email')" v-validate="'email'" data-vv-name="email"></v-text-field> -->
                   
                    <!-- <v-text-field v-model="customer.coustomerQuery" label="Enter Query" :error-messages="errors.collect('coustomerAddress')" v-validate="'required'" data-vv-name="coustomerAddress" required></v-text-field> -->
                    <br>
                    <div>
                     <input type="file" v-on:change="handleFileInputChange">
                   </div>
                   <br>
                    <v-btn block large color="info" @click="submit" >Submit </v-btn>
                </v-flex>
            </v-layout>
            <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">Do you what to send Query?</v-card-title>

        <v-card-text>
          Confirmation 
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="dialog = false"
          >
            Disagree
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="submitForm"
          >
            Agree
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
        </v-container>
    </div>
</template>
<script>
import aadhaarValid from "aadhaar-validator";
  import axios from 'axios'
export default {
  $_veeValidate: {
    validator: "new"
  },
  name: "CrateUser",
  data() {
    return {
      userName: "",
      dialog:false,
      mobileNumber: null,
      accountNumber: null,
      ifscCode: null,
      aadhaarNO: null,
      bankName: "",
      email: "",
      customer: {},
      select: null,
      items: ["Item 1", "Item 2", "Item 3", "Item 4"],
      checkbox: null,
      dictionary: {
        attributes: {
          email: "E-mail Address"
        },
        custom: {
          name: {
            required: () => "Name can not be empty"
          },
          select: {
            required: "Select field is required "
          },
          mobile: {
            required: "mobile number field is required",
            max: "The mobile number field may not be greater than 10 digit",
            numeric: "Please enter Number only."
          },
          accountno: {
            numeric: "Please enter Number only.",
            required: "Account number field is required",
            max: "The Account number field may not be greater than 16 digit",
            min: "The Account number field may not be less than 11 digit"
          },
          ifsc: {
            required: "IFSC Code is required",
            regex: "Invalid IFSC Code"
          },
          aadhar: {
            required: "Please Enter Valid Aadhar number ",
            max: "The Aadhar number field may not be greater than 12 digit"
          },
          branch: {
            required: "Branch name required "
          },
           coustomerAddress: {
            required: "Coustomer Address  required "
          },
          coustomerQuery:{
            required: "Query filed required "
          }
        }
      }
    }
  },
  mounted() {
    this.$validator.localize("en", this.dictionary);
  },

  methods: {
    submit() {
      this.$validator.validateAll().then((result) => {
        if(result == true){
  // axios.post(`/userRegistration`, this.customer).then(response => {
  //   // console.log(JSON.stringify(response.data.result.ops[0]));
  //   if(response.data.status === true){
  //     this.$toasted.show('Registration Success.',{ theme: "outline", position: "top-center", duration : 1500})
  //     this.customer= {}
  //     this.$validator.reset();
  //   }else{
  //     this.$toasted.show('Registration Fail.',{ theme: "outline", position: "top-center", duration : 1500})
  //   }
  //   }).catch(e => {
  //     this.errors.push(e)
  //   })
  this.dialog = true
  
    }
  })
    },
    validateAdhar() {
        if(this.aadhaarNO != null){
      var valid = aadhaarValid.isValidNumber(this.aadhaarNO)
      if(!valid){
      this.aadhaarNO = null
      }
    }
    },
    submitForm(){
      this.$router.push('dashboardVoting') 
    },
    handleFileInputChange(event) {
      // Access the file through event.target.files
      const file = event.target.files[0];
      
      // Do whatever you need with the file
      console.log('Selected file:', file);
    }
  }
};
</script>

<style>

</style>



